<template>
    <div class="login" :style="winSize">
 
        <el-row>
            <el-col :span='24'>
                <div class="content">
                    <el-form label-position="left" label-width="0px" class="demo-ruleForm card-box loginform"
                        v-loading="login_actions.disabled"
                        element-loading-text="正在注册..."
                        :style="formOffset"
                        :model='data'
                        :rules="rule_data"
                        ref='data'>
                        <h3 class="title">系统注册</h3>


                          <el-form-item  prop='email'>
                            <el-input  v-model="data.email" placeholder="请输入邮箱"></el-input>
                         </el-form-item>



                        <el-form-item
                            prop='password'>
                            <el-input type="password" auto-complete="off" placeholder="密码" 
                                v-model='data.password'
                                @keyup.native.enter="login('data')"></el-input>
                        </el-form-item>

                             <el-form-item
                            prop='retpassword'>
                            <el-input type="password" auto-complete="off" placeholder="确认密码" 
                                v-model='data.retpassword'
                                @keyup.native.enter="login('data')"></el-input>
                           </el-form-item>

                               <el-form-item  prop='phome' style="width:100%;" :inline="true">
                            <el-input  v-model="data.phone" placeholder="请输入手机号" style="width:80%;"></el-input>
                            <el-button :plain="true" :disabled="true">30</el-button>
                         </el-form-item>

                                <el-form-item  prop='zuzhi'>
                            <el-input  v-model="data.zuzhi" placeholder="请输入组织机构代码"></el-input>
                         </el-form-item>


                                <el-form-item  prop='shangpu'>
                            <el-input  v-model="data.shangpu" placeholder="请输入商铺名称"></el-input>
                         </el-form-item>

                           <el-form-item style="width:100%;" :inline="true">

    <el-select v-model="data.region" placeholder="地址" inline="true">
      <el-option label="区域一" value="shanghai"></el-option>
      <el-option label="区域二" value="beijing"></el-option>
    </el-select>

     <el-select v-model="data.region2" placeholder="请选择活动区域" inline="true">
      <el-option label="区域一" value="shanghai"></el-option>
      <el-option label="区域二" value="beijing"></el-option>
    </el-select>
      <el-input  v-model="data.shangpu" placeholder="请输入详细地址"></el-input>
  </el-form-item>

上传营业执照
<el-upload
  class="avatar-uploader"
  action="https://jsonplaceholder.typicode.com/posts/"
  :show-file-list="false"
  :on-success="handleAvatarSuccess"
  :before-upload="beforeAvatarUpload">
  <img v-if="imageUrl" :src="imageUrl" class="avatar">
  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
</el-upload>
                         

                     
                        <el-form-item style="width:100%;">
                            <el-button type="primary" @click='login("data")'>登录</el-button>
                            <el-button @click='resetForm("data")'>重置</el-button>
                             <el-button @click='reg()'>注册</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import RegJs from './Reg.js';
    module.exports=RegJs;
</script>

<style scoped lang='less'>
    .login{
        background: #1F2D3D;

        .card-box {
            box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
            -webkit-border-radius: 5px;
            border-radius: 5px;
            -moz-border-radius: 5px;
            background-clip: padding-box;
            margin-bottom: 20px;
            background-color: #F9FAFC;
            border: 2px solid #8492A6;
        }

        .title {
            margin: 0px auto 40px auto;
            text-align: center;
            color: #505458;
        }

         .loginform {
            width: 350px;
            padding: 35px 35px 15px 35px;
            .avatar-uploader {
         
                .el-upload {
                    width:899px;
                    border: 1px dashed #d9d9d9;
                    border-radius: 6px;
                    cursor: pointer;
                    position: relative;
                    overflow: hidden;
                }
                .el-upload:hover {
                    border-color: #20a0ff;
                }
                .avatar-uploader-icon {
                    font-size: 28px;
                    color: #8c939d;
                    width: 178px;
                    height: 178px;
                    line-height: 178px;
                    text-align: center;
                }
                .avatar {
                    width: 178px;
                    height: 178px;
                    display: block;
                }
        }
    } 
        
        

}


</style>



