import {
    Login,
    Reg,
    Home,
    NotFound,
    Content,
    Modules
} from '../components/';

module.exports = [{
    path: '/',
    redirect: to => {
        return 'login';
    },
    hidden: true
}, {
    path: '/login',
    component: Login,
    hidden: true
}, 
 {
    path: '/reg',
    component: Reg,
    hidden: true
},
{
    path: '/404',
    component: Home,
    hidden: true,
    children: [{
        path: '',
        component: NotFound
    }]
}, {
    path: '/mfd',
    name: '后台管理',
    icon: 'inbox',
    component: Home,
    children: [{
        hidden: true,
        path: '',
        redirect: to => {
            return 'user'
        }
    }, {
        path: 'user',
        name: '用户管理',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'list'
            }
        }, {
            path: 'list',
            name: '用户列表',
            icon: 'reorder',
            component: Modules.Demo.User.List
        }]
    },  {
        path: 'order',
        name: '订单管理',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'list'
            }
        }, {
            path: 'list',
            name: '订单列表',
            icon: 'reorder',
            component: Modules.Demo.Order.List
        }]
    },
    // {
    //     path: 'orderstatis',
    //     name: '订单统计',
    //     icon: 'inbox',
    //     component: Content,
    //     children: [{
    //         hidden: true,
    //         path: '',
    //         redirect: to => {
    //             return 'bar'
    //         }
    //     }, {
    //         path: 'bar',
    //         name: '柱状图',
    //         icon: 'reorder',
    //         component: Modules.Demo.OrderStatis.Bar
    //     }, {
    //         path: 'pie',
    //         name: '饼状图',
    //         icon: 'edit',
    //         component: Modules.Demo.OrderStatis.Pie
    //     }]
    // }
    ]
}];


